import * as actionTypes from '../constants/UserConstants';

const initialState = {
  userData:
  {
    firstName: 'John',
    lastName: 'Wick',
    email: 'john@adapty.io',
    facebook: {
      id: '1111111111',
      name: 'Adapty Ad Account'
    },
    my_target: false,
    team: {
      name: 'Adapty team',
      mates: [
        {
          email: 'corgi@adapty.io',
          status: true,
          id: 1
        },
        {
          email: 'kp@adapty.io',
          status: false,
          id: 2
        }
      ]
    },
    social: [
      {
        name: 'Telegram',
        status: true
      },
      {
        name: 'Slack',
        status: false
      }
    ]
  }
};

export default function userReducer(state = initialState, action) {
  const {
    type,
    userData
  } = action;

  switch (type) {
    case actionTypes.GET_USER_DATA:
    case actionTypes.SET_USER_TEAMMATE:
    case actionTypes.SET_STATUS_TEAMMATE:
    case actionTypes.REMOVE_USER_TEAMMATE:
    case actionTypes.ADD_USER_TEAMMATE:
      return {
        ...state,
        userData
      };

    default:
      return state;
  }
}



