export default function companyReducer(state = initialState, action) {
  const {
    type,
    companyData,
    company,
    rule
  } = action;

  switch (type) {
    case actionTypes.GET_COMPANY_DATA:
    case actionTypes.SET_COMPANY_STATUS:
      return {
        ...state,
        companyData
      };
    case actionTypes.GET_COMPANY_OBJECT:
      return {
        ...state,
        company
      };
    case actionTypes.GET_RULE_OBJECT:
      return {
        ...state,
        rule
      };

    default:
      return state;
  }
}



