import './App.styl';

import injectTapEventPlugin from 'react-tap-event-plugin';
try { injectTapEventPlugin(); } catch (e) { console.log(e); }

import React from 'react';
import Type from 'prop-types';

import ProjectRawTheme from '../../theme/material_ui_raw_theme';
import { getMuiTheme } from 'material-ui/styles';

import { asyncComponent } from 'react-async-component';
import { Route, Switch } from 'react-router-dom';

const LoginPage = asyncComponent({
  resolve: () => System.import('../LoginPage/LoginPage')
});

const ContactUs = asyncComponent({
  resolve: () => System.import('../ContactUs/ContactUs')
});

const ProfilePage = asyncComponent({
  resolve: () => System.import('../ProfilePage/ProfilePage')
});

const HelpPage = asyncComponent({
  resolve: () => System.import('../HelpPage/HelpPage')
});

const ReportsPage = asyncComponent({
  resolve: () => System.import('../ReportsPage/ReportsPage')
});

const RulesPage = asyncComponent({
  resolve: () => System.import('../RulesPage/RulesPage')
});

const DashboardPage = asyncComponent({
  resolve: () => System.import('../DashboardPage/DashboardPage')
});

const RuleSettingsPage = asyncComponent({
  resolve: () => System.import('../RuleSettingsPage/RuleSettingsPage')
});

export default class App extends React.Component {
  static propTypes = {
    children: Type.array
  }

  static get childContextTypes() {
    return { muiTheme: Type.object };
  }

  getChildContext() {
    return { muiTheme: getMuiTheme(ProjectRawTheme) };
  }

  render() {
    return (
      <div className="app">
        <Switch>
          <Route key="/" exact={true} path="/" component={LoginPage} />
          <Route key="/registration" exact={true} path="/registration" component={LoginPage} />
          <Route key="/profile" exact={true} path="/profile" component={ProfilePage} />
          <Route key="/help" exact={true} path="/help" component={HelpPage} />
          <Route key="/help/two" exact={true} path="/help/two" component={HelpPage} />
          <Route key="/help/three" exact={true} path="/help/three" component={HelpPage} />
          <Route key="/help/four" exact={true} path="/help/four" component={HelpPage} />
          <Route key="/help/contact-us" exact={true} path="/help/contact-us" component={ContactUs} />
          <Route key="/reports" exact={true} path="/reports" component={ReportsPage} />
          <Route key="/rules" exact={true} path="/rules" component={RulesPage} />
          <Route key="/edit-campaing/:id" exact={true} path="/edit-campaing/:id" component={RulesPage} />
          <Route key="/dashboard" exact={true} path="/dashboard" component={DashboardPage} />
          <Route key="/rule-settings" exact={true} path="/rule-settings" component={RuleSettingsPage} />
          <Route key="/edit-rule/:id" exact={true} path="/edit-rule/:id" component={RuleSettingsPage} />
          <Route key="/new-report" exact={true} path="/new-report" component={ReportsPage} />
        </Switch>
      </div>
    );
  }
}



