import * as actionTypes from '../constants/ReportConstants';

const initialState = {
  reportData: [
    {
      name: 'Installs report',
      facebook: true,
      collection: 'Every day, every week',
      status: true
    },
    {
      name: 'Website visitors dynamics report',
      facebook: true,
      collection: 'Every 4 hours, every week',
      status: false
    },
  ]
};

export default function reportReducer(state = initialState, action) {
  const {
    reportData,
    type
  } = action;

  switch (type) {
    case actionTypes.GET_REPORT_DATA:
    case actionTypes.SET_REPORT_STATUS:
      return {
        ...state,
        reportData
      };

    default:
      return state;
  }
}



